@import '_index.scss';

*{
    font-size: 100%;
    text-decoration: none;
    list-style: none;
    box-sizing: border-box;
    border: 0;
    padding: 0;
    margin: 0;
    scroll-behavior: smooth;
}

a,
a:visited,
a:link,
a:hover{
    color: unset;
    text-decoration: none;
    list-style: none;
}

body{
    font-size: 16px;
    font-weight: 400;
    font-family: $Manrope;
    background: $white;
    scroll-behavior: smooth;
}

.heading{
    color: #000000;
    font-size: 2.5rem;
    font-weight: 600;
    line-height: 3.4375rem;
}