@import '../../_index.scss';

.header{
    display: flex;
    flex-direction: row;
    column-gap: 5rem;
    width: 90%;
    margin: 2rem auto 15rem auto;

    @include breakpointMax(md){
        flex-direction: column;
        width: 100%;
    }

    &-content{

        @include breakpointMax(md){
            width: 80%;
            margin: 0 auto;
        }

        &--orange{
            color: #FF9900;
            font-family: $Dm-Sans;
            font-weight: bold;
            font-size: 1.375rem;
            line-height: 1.8125rem;

            @include breakpointMax(lg){
                font-size: 1.2rem;
            }

            @include breakpointMax(sm){
                font-size: 1rem;
            }
        }

        &__heading{
            color: #000000;
            font-style: normal;
            font-weight: 800;
            font-size: 3.375rem;
            line-height: 4.5625rem;
            padding: 1.5rem 0;

            @include breakpointMax(lg){
                font-size: 2.5rem;
                line-height: 3rem;
            }

            @include breakpointMax(md){
                font-size: 2rem;
                line-height: 2.2rem;
            }
        }

        &__text{
            font-style: normal;
            font-weight: normal;
            font-size: 1.0625rem;
            line-height: 23px;
            letter-spacing: 0.01em;
            color: #31353B;
            width: 95%;
            margin-bottom: 3rem;

            @include breakpointMax(lg){
                font-size: 0.9rem;
            }

            @include breakpointMax(sm){
                font-size: 0.9rem;
            }
        }

        &__input{
            width: 100%;
            display: flex;
            align-items: center;
            justify-content: space-between;

            height: 5.375rem;
            box-shadow: 0px 100px 80px rgba(0, 0, 0, 0.03), 0px 64.8148px 46.8519px rgba(0, 0, 0, 0.0227778), 0px 38.5185px 25.4815px rgba(0, 0, 0, 0.0182222), 0px 20px 13px rgba(0, 0, 0, 0.015), 0px 8.14815px 6.51852px rgba(0, 0, 0, 0.0117778), 0px 1.85185px 3.14815px rgba(0, 0, 0, 0.00722222);

            &_img{
                margin: 0 1rem;
                height: 1.25rem;

                @include breakpointMax(sm){
                    display: none;
                }
            }
            
            &_left{
                display: flex;
                align-items: center;
                width: 100%;

                input{
                    width: 100%;
                    height: 5rem;
                    outline: none;
                    z-index: 10;

                    @include breakpointMax(lg){
                        font-size: 13px;
                    }

                    @include breakpointMax(lg){
                        padding-left: 1rem;
                        width: 90%;
                    }
                }
            }

            &_btn{
                color: #FFFFFF;
                font-size: 1.0625rem;
                font-weight: bold;
                letter-spacing: 0.02rem;
                line-height: 1.4375rem;
                z-index: 10;
                width: 25%;
                height: 5.375rem;
                background: $orange;

                @include breakpointMax(lg){
                    font-size: 0.8rem;
                    width: 20%;
                }

                @include breakpointMax(md){
                    font-size: 0.85rem;
                }

                @include breakpointMax(sm){
                    font-size: 0.9rem;
                    line-height: 1rem;
                    width: 40%;
                }
            }
        }

        &__brand{
            display: flex;
            flex-direction: column;
            margin: 4rem 0;

            &_text{
                color: $text;
                font-size: 17px;
                line-height: 23px;
                letter-spacing: 0.01em;
                margin-bottom: 1.6875rem;
            }

            &_items{
                display: flex;
                flex-direction: row;
                flex-wrap: wrap;
                row-gap: 2rem;
                column-gap: 2rem;
                justify-content: center;
            }
        }
    }

    &-container{
        display: flex;
        flex-direction: row;
        align-items: center;

        @include breakpointMax(md){
            justify-content: center;
            margin-top: 6rem;
            position: relative;
        }

        img{
            width: 90%;

            @include breakpointMax(lg){
                width: 95%;
            }

            @include breakpointMax(md){
                width: 70%;
            }
        }

        &__social{
            display: flex;
            flex-direction: column;
            margin-left: 10%;
            align-self: center;

            img{
                width: 20px;
                margin-bottom: 1.625rem;
            }
        }

        &__bg{
            height: 100vh;
            width: 35%;
            background: $cyan;
            top: 0;
            right: 0;
            position: absolute;
            z-index: -1;
    
            @include breakpointMax(lg){
                height: 100%;
            }

            @include breakpointMax(md){
                height: 130%;
                margin-top: -10%;
            }
        }
    }
}